import React,{useState} from 'react'
import { Link } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CRow,
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import axios from 'axios'
import { cilLockLocked, cilUser } from '@coreui/icons'


const Login = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [fomrResponse,setFormResponse]= React.useState({email:"",password:""})
  const handleLogin = (values) => {
    if(!fomrResponse.email){
      return alert("email not provided")
    }
    if(!fomrResponse.password){
      return alert("password not provided")
    }
    setIsLoading(true);
    axios
      .post(`${process.env.REACT_APP_API_URL}/users/AdminLogin`, fomrResponse)
      .then((res) => {
        setIsLoading(false);
    
const token= res.data.token
const userData= res.data.userData
Promise.resolve(localStorage.setItem("currentUser",JSON.stringify({token,userData}))).then(window.location.reload())
      })
      .catch((err) => {
        setIsLoading(false);
        if (err.response) {
        
          err.response.data.message &&
            alert(err.response.data.message);
        
        }
        console.log(err);
      });
  };
  
  return (
    <div className="bg-light min-vh-100 d-flex flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md={8}>
            <CCardGroup>
              <CCard className="p-4">
                <CCardBody>
                  <CForm>
                    <h1>Login</h1>
                    <p className="text-medium-emphasis">Sign In to your account</p>
                    <CInputGroup className="mb-3">
                      <CInputGroupText>
                        <CIcon icon={cilUser} />
                      </CInputGroupText>
                      <CFormInput  onChange={(e)=>setFormResponse({...fomrResponse,email:e.target.value})} placeholder="Username" autoComplete="username" />
                    </CInputGroup>
                    <CInputGroup className="mb-4">
                      <CInputGroupText>
                        <CIcon icon={cilLockLocked} />
                      </CInputGroupText>
                      <CFormInput onChange={(e)=>setFormResponse({...fomrResponse,password:e.target.value})}
                        type="password"
                        placeholder="Password"
                        autoComplete="current-password"
                      />
                    </CInputGroup>
                    <CRow>
                      <CCol xs={6}>
                        <CButton onClick={handleLogin} color="primary" className="px-4">
                          Login
                        </CButton>
                      </CCol>
                      <CCol xs={6} className="text-right">
                        <CButton color="link" className="px-0">
                          Forgot password?
                        </CButton>
                      </CCol>
                    </CRow>
                  </CForm>
                </CCardBody>
              </CCard>
              <CCard className="text-white bg-primary py-5" style={{ width: '44%' }}>
                <CCardBody className="text-center">
                  <div>
                    <p>
                      Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod
                      tempor incididunt ut labore et dolore magna aliqua.
                    </p>
                  </div>
                </CCardBody>
              </CCard>
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  )
}

export default Login
